
import React, { Component } from 'react';
import { Layout } from 'antd';
import { withRouter } from 'react-router-dom';
import logo from '../style/imgs/logo.png';
import { Menu, Icon } from 'antd';
import { Link } from 'react-router-dom';
const SubMenu = Menu.SubMenu;
const { Sider } = Layout;

class SiderCustom extends Component {
    state = {
        collapsed: false,
        mode: 'inline',
        openKey: '',
        selectedKey: '',
        firstHide: true,        // openMenu
    };
    componentDidMount() {
        this.setMenuOpen(this.props);
    }
    componentWillReceiveProps(nextProps) {
        console.log(nextProps);
        this.onCollapse(nextProps.collapsed);
        this.setMenuOpen(nextProps)
    }
    setMenuOpen = props => {
        const { pathname } = props.location;
        this.setState({
            openKey: pathname.substr(0, pathname.lastIndexOf('/')),
            selectedKey: pathname
        });
    };
    onCollapse = (collapsed) => {
        this.setState({
            collapsed,
            firstHide: collapsed,
            mode: collapsed ? 'vertical' : 'inline',
        });
    };
    menuClick = e => {
        this.setState({
            selectedKey: e.key
        });
        const { popoverHide } = this.props;     // 
        popoverHide && popoverHide();
    };
    openMenu = v => {
        this.setState({
            openKey: v[v.length - 1],
            firstHide: false,
        })
    };
    render() {
        const {openKey} = this.state;
        let openKeyKey = ['/app/ranking'],openKeyArr = [];
        if (openKey == '/app/ranking/order' || openKey == '/app/ranking/driver' || openKey == '/app/ranking/user'){
            openKeyKey.push(openKey);
            openKeyArr = openKeyKey;
        }else {
            openKeyArr.push(openKey)
        }
        console.log(openKeyArr)
        return (
            <Sider
                trigger={null}
                collapsed={this.props.collapsed}
                style={{ overflowY: 'auto' }}
            >
                <div className="logo">
                    <Link to="/">
                        <img src={logo} alt="logo"/>
                        <span className="logo-title"></span>
                    </Link>
                </div>
                <Menu theme="dark"
                      mode="inline"
                      selectedKeys={[this.state.selectedKey]}
                      // openKeys={this.state.firstHide ? null : [this.state.openKey]}
                        openKeys = {this.state.firstHide ? null : openKeyArr}
                      onClick={this.menuClick}
                      onOpenChange={this.openMenu}
                      inlineCollapsed={this.state.collapsed}
                >
                    <SubMenu key="/app/realtime"
                             title={<span>
                                         <Icon type="clock-circle-o"/>
                                         <span className="nav-text"></span></span>}
                    >
                        <Menu.Item key="/app/realtime/survey"><Link to={`/app/realtime/survey`}></Link></Menu.Item>
                        <Menu.Item key="/app/realtime/order"><Link to={`/app/realtime/order`}></Link></Menu.Item>
                    </SubMenu>
                    <SubMenu key="/app/operatingdaily" title={<span><Icon type="schedule"/><span className="nav-text"></span></span>}>
                        <Menu.Item key="/app/operatingdaily/incomeCost"><Link to={`/app/operatingdaily/incomeCost`}></Link></Menu.Item>
                        <Menu.Item key="/app/operatingdaily/distribution"><Link to={`/app/operatingdaily/distribution`}></Link></Menu.Item>

                        <Menu.Item key="/app/operatingdaily/portrait"><Link to={`/app/operatingdaily/portrait`}></Link></Menu.Item>
                        <Menu.Item key="/app/operatingdaily/bargainingAnalysis"><Link to={`/app/operatingdaily/bargainingAnalysis`}></Link></Menu.Item>

                        <Menu.Item key="/app/operatingdaily/capacityAnalysis"><Link to={`/app/operatingdaily/capacityAnalysis`}></Link></Menu.Item>
                        <Menu.Item key="/app/operatingdaily/userStatistics"><Link to={`/app/operatingdaily/userStatistics`}></Link></Menu.Item>
                        <Menu.Item key="/app/operatingdaily/piesAnalysis"><Link to={`/app/operatingdaily/piesAnalysis`}></Link></Menu.Item>
                        <Menu.Item key="/app/operatingdaily/serviceQualityOfDrivers"><Link to={`/app/operatingdaily/serviceQualityOfDrivers`}></Link></Menu.Item>
                    </SubMenu>

                    <SubMenu key="/app/ranking" title={<span><Icon type="flag"/><span  className="nav-text"></span></span>}>
                        <SubMenu key="/app/ranking/order" title="">
                            <Menu.Item key="order_0"><Link to={`/app/ranking/order/order_0`} replace></Link></Menu.Item>
                            <Menu.Item key="order_1"><Link to={`/app/ranking/order/order_1`}></Link></Menu.Item>
                            <Menu.Item key="order_2"><Link to={`/app/ranking/order/order_2`}></Link></Menu.Item>
                            <Menu.Item key="order_3"><Link to={`/app/ranking/order/order_3`}></Link></Menu.Item>
                            <Menu.Item key="order_4"><Link to={`/app/ranking/order/order_4`}></Link></Menu.Item>
                            <Menu.Item key="order_5"><Link to={`/app/ranking/order/order_5`}>C to R</Link></Menu.Item>
                            <Menu.Item key="order_6"><Link to={`/app/ranking/order/order_6`}></Link></Menu.Item>
                            <Menu.Item key="order_7"><Link to={`/app/ranking/order/order_7`}></Link></Menu.Item>
                            <Menu.Item key="order_8"><Link to={`/app/ranking/order/order_8`}></Link></Menu.Item>
                            <Menu.Item key="order_9"><Link to={`/app/ranking/order/order_9`}></Link></Menu.Item>
                            <Menu.Item key="order_10"><Link to={`/app/ranking/order/order_10`}></Link></Menu.Item>
                        </SubMenu>
                        <SubMenu key="/app/ranking/driver" title="">
                            <Menu.Item key="driver_0"><Link to={`/app/ranking/driver/driver_0`} replace></Link></Menu.Item>
                            <Menu.Item key="driver_1"><Link to={`/app/ranking/driver/driver_1`}></Link></Menu.Item>
                            <Menu.Item key="driver_2"><Link to={`/app/ranking/driver/driver_2`}></Link></Menu.Item>
                            <Menu.Item key="driver_3"><Link to={`/app/ranking/driver/driver_3`}></Link></Menu.Item>
                            <Menu.Item key="driver_4"><Link to={`/app/ranking/driver/driver_4`}></Link></Menu.Item>
                            {/*<Menu.Item key="driver_5"><Link to={`/rank/driver/driver_5`} ></Link></Menu.Item>*/}
                        </SubMenu>
                        <SubMenu key="/app/ranking/user" title="">
                            <Menu.Item key="user_0"><Link to={`/app/ranking/user/user_0`} replace></Link></Menu.Item>
                            <Menu.Item key="user_1"><Link to={`/app/ranking/user/user_1`}></Link></Menu.Item>
                            <Menu.Item key="user_2"><Link to={`/app/ranking/user/user_2`}></Link></Menu.Item>
                            <Menu.Item key="user_3"><Link to={`/app/ranking/user/user_3`}></Link></Menu.Item>
                            <Menu.Item key="user_4"><Link to={`/app/ranking/user/user_4`}></Link></Menu.Item>
                            {/*<Menu.Item key="user_5"><Link to={`/rank/user/user_5`} ></Link></Menu.Item>*/}
                            <Menu.Item key="user_6"><Link to={`/app/ranking/user/user_6`}></Link></Menu.Item>
                        </SubMenu>
                    </SubMenu>
                </Menu>
                <style>
                    {`
                    #nprogress .spinner{
                        left: ${this.state.collapsed ? '70px' : '206px'};
                        right: 0 !important;
                    }
                    .ant-layout .logo {
                        height: 64px;
                        line-height: 64px;
                        background: #002140;
                        margin: 0;
                        padding-left: 14px;
                    }
                    .logo-title {
                        color: #fff;
                        font-size: 20px;
                        font-weight: 600;
                    }
                    .logo img {
                        display: inline-block;
                        width: 45px;
                        height: 45px;
                    }
                    .logo i img {
                        width: 100%;
                    }
                    `}
                </style>
            </Sider>
        )
    }
}

export default withRouter(SiderCustom);


