
import React from 'react';
import {Card, Table, Radio, Row, Col, Button, Pagination} from 'antd';
import moment from 'moment';
import SearchBox from '../../components/searchBox/searchBox'
import ExportFileCom from '../../components/exportFile/exportFile'

import {getFun} from '../../utils/api'
import {objectToArr, dateDiff} from '../../utils/dataHandle'
import './operating.less'

const RadioButton = Radio.Button;
const RadioGroup = Radio.Group;

class Portrait extends React.Component{
    constructor(props) {
        super(props);
        this.state={
            title: '',
            total: 10,
            pageSize: 10,
            current: 1,
            load: true,
            dayNum: 10,
            tableData: [],
            selectValue: '',
            carCombine: { // 
                0: [],
                1: [37, 78],
                2: [2,3],
                3: [5],
                4: [78],
                5: [37, 78, 2, 3, 5]
            },
            carTypes: { //
                0: '',
                1: '+',
                2: '+',
                3: '+',
                4: '',
                5: ''
            },
            city: '',
            start_at: '',
            end_at: '',
            car_type_id: '',
            searchParams: {},
            tableHeader: [
                {
                    title: '', dataIndex: 'start_time', key: 'start_time'
                },
                {
                    title: '',
                    children: [
                        {title: '', dataIndex: 'total_of_orders', key: 'total_of_orders'},
                        {title: '', dataIndex: 'total_of_dispatch_orders', key: 'total_of_dispatch_orders'},
                        {title: '', dataIndex: 'total_of_active_decision_orders', key: 'total_of_active_decision_orders'},
                        {title: '', dataIndex: 'total_of_dispatch_intraday_finished_orders', key: 'total_of_dispatch_intraday_finished_orders'}
                    ]
                },
                {
                    title: '',
                    children: [
                        {title: '(km)', dataIndex: 'kongshi_average_distance', key: 'kongshi_average_distance'},
                        {title: '(min)', dataIndex: 'kongshi_average_time', key: 'kongshi_average_time'}
                    ]
                },
                {
                    title: '',
                    children: [
                        {title: '(km)', dataIndex: 'order_average_distance', key: 'order_average_distance'},
                        {title: '(min)', dataIndex: 'order_average_time', key: 'order_average_time'}
                    ]
                },
                {
                    title: '',
                    children: [
                        {title: '', dataIndex: 'total_of_bymeter_orders', key: 'total_of_bymeter_orders'},
                        {title: '(%)', dataIndex: 'rate_of_bymeter_order', key: 'rate_of_bymeter_order'},
                        {title: '', dataIndex: 'average_amount_of_bymeter_order', key: 'average_amount_of_bymeter_order'}
                    ]
                }
            ],
            exportParams: {}
        }
    }
    componentWillMount() {
        this.initDateRange(this.state.dayNum);//
    }
    componentDidMount(){
        const params = {
            city: '',
            start_at: this.state.start_at,
            end_at: this.state.end_at, //n
            car_type_id: ''
        }
        this.setState({
            load:true
        },() => {
            this.getTableData(params);
        })
    }
    //
    initDateRange(rangeDays) {
        //moment
        const  endTime= moment().subtract(1, 'days');//
        const startTime = moment().subtract(rangeDays, 'days');//
        const start = new Date((moment(startTime).subtract())._d);
        const end = new Date((moment(endTime).subtract())._d);
        this.setState({
            city: '',
            start_at: this.formatDate(start),
            end_at: this.formatDate(end), //n
            car_type_id: ''
        }, () => {this.initExportData()});
    }
    // 
    initExportData() {
        const exportParams = {
            start_at: this.state.start_at,
            end_at: this.state.end_at,
            title: this.state.title,
            city: this.state.city,
            car_type_id: this.state.car_type_id,
            tableHeader: this.state.tableHeader,
            exportData: this.state.tableData
        }
        this.setState({
            exportParams: exportParams
        })
    }
    // 
    formatDate (date) {
        var y = date.getFullYear();
        var m = date.getMonth() + 1;
        m = m < 10 ? '0' + m : m;
        var d = date.getDate();
        d = d < 10 ? ('0' + d) : d;
        return y + '-' + m + '-' + d;
    };
    // 
    searchParams(params){

        this.setState({
            city: params.city,
            start_at: params.selectedStartDate,
            end_at: params.selectedEndDate
        })
    }
    // 
    carTypeChange(e) {
        let index = e.target.value;
        this.setState({
            car_type_id: this.state.carCombine[index].join(',')
        },() => {
            this.searchBtn()
        })
    }
    // 
    searchBtn() {
        this.setState({
            load: true,
            total: this.getTotalPage()
        },() => {
            this.getTableData()
        })
    }
    // 
    pageChange(page, pageSize) {
        this.setState({
            current: page,
            pageSize: pageSize,
            load: true
        },() => {
            this.getTableData()
        })
    }
    onShowSizeChange(current, size) {
        this.setState({
            pageSize: size,
            current: current,
            load: true
        }, () => {
            this.getTableData();
        });
    }
    // 
    getTableData() {
        let searchParams = this.getParams();
        let result =getFun('/web_api/operation/portrait',  searchParams);
        result.then(res => {
            this.setState({
                load: false,
                tableData: objectToArr(res.data)

            })
        }).catch(err => {
            console.log(err)
        })
    }
    // 
    getParams() {
        let start, end;
        start = this.pageStartDate().format("YYYY-MM-DD");
        end = this.pageEndDate().format("YYYY-MM-DD");
        const params = {
            start_at: start,
            end_at: end,
            city: this.state.city,
            car_type_id: this.state.car_type_id
        }
        return params;
    }
    //
    pageEndDate() {
        let days = (this.state.current - 1) * this.state.pageSize;
        let copy = moment(this.state.end_at).add(0, 'days'); //
        return  copy.subtract(days, 'days');
    }

    //
    pageStartDate() {
        let days = this.state.current * this.state.pageSize;
        let copy, dt;
        copy = moment(this.state.end_at); //
        copy = copy.add(1, 'days'); //+1
        dt = copy.subtract(days, 'days');
        if(this.state.start_at && dt< this.state.start_at) {
            return this.state.start_at;
        }
        return dt;
    }
    getTotalPage() {
        let day = dateDiff(this.state.start_at, this.state.end_at);
        return day;
    }
    render() {
        let {title, carTypes, tableData, load, tableHeader, total, pageSize} = this.state;
        const radioChildren = Object.keys(carTypes).map((key, index) => {
            return <RadioButton key={key} value={key}>{carTypes[key]}</RadioButton>
        });
        return (
            <div>
                <div className="operating-wrapper">
                    <Card title={title}  bordered={false}>

                        <Row gutter={16}>
                            <Col span={14}>
                                <div>
                                    <SearchBox searchParams={params => this.searchParams(params)}></SearchBox>
                                </div>
                                <div className="cartype-wrapper">
                                    <RadioGroup onChange={this.carTypeChange.bind(this)} defaultValue='0' >
                                        {radioChildren}
                                    </RadioGroup>
                                </div>
                            </Col>
                            <Col span={10}>
                                <Button type="primary" onClick={this.searchBtn.bind(this)}></Button>
                            </Col>
                        </Row>
                        <div>
                            <Table dataSource={tableData} bordered loading={load} columns={tableHeader} pagination={false}>

                            </Table>
                        </div>
                        <div className="page-footer">
                            <Row>
                                <Col span={10}>
                                    <ExportFileCom params={this.state.exportParams}></ExportFileCom>
                                </Col>
                                <Col span={14} style={{textAlign: 'right'}}>
                                    <Pagination size="small" total={total} onChange={this.pageChange.bind(this)} pageSize={pageSize} showSizeChanger={true} onShowSizeChange={this.onShowSizeChange.bind(this)} showQuickJumper></Pagination>
                                </Col>
                            </Row>
                        </div>
                    </Card>
                </div>
            </div>
        )
    }
}
export default Portrait;


